<template lang="html">
  <div class="">
    <el-form :model="registerValidateForm" label-width="100px"  ref="registerValidateForm">
          <el-form-item
            prop="email"
            label=""
            :rules="rules.email"
          >
            <el-input v-model="registerValidateForm.email"></el-input>
          </el-form-item>
          <el-form-item
          prop="password"
          label=""
          :rules = "rules.password"
          >
            <el-input type="password" v-model="registerValidateForm.password"></el-input>
          </el-form-item>
          <el-form-item
          prop="checkPass"
          label=""
          :rules = "rules.checkPass"
          >
            <el-input type="password" v-model="registerValidateForm.checkPass"></el-input>
          </el-form-item>
          <el-button type="primary" @click="submitForm('registerValidateForm')"></el-button>
          <el-button @click="resetForm('registerValidateForm')"></el-button>
    </el-form>
  </div>
</template>

<script>
import * as types from '../store/mutation-types'
import api from '../store/modules/login'
export default {
    name: 'login',
    data() {
        // 
        let validatePass1 = (rule, value, callback) => {
            // 6-16, , , , 
            let reg = /(?!^[0-9]+$)(?!^[A-z]+$)(?!^[^A-z0-9]+$)^[^\s\u4e00-\u9fa5]{6,16}$/;
            if (!reg.test(value)) {
                callback(new Error('6-16'))
            } else {
                callback()
            }
        };
        // 
        let validatePass2 = (rule, value, callback) => {
            value === '' ? callback(new Error('')) :
                value !== this.registerValidateForm.password ? callback(new Error('!')) :
                callback()
        };
        return {
            registerValidateForm: {
                email: '',
                password: '',
                checkPass: '',
                first: 'first'
            },
            rules: {
                email: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        type: 'email',
                        message: '',
                        trigger: 'blur'
                    }
                ],
                password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        validator: validatePass1,
                        trigger: 'blur'
                    }
                ],
                checkPass: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        validator: validatePass2,
                        trigger: 'blur'
                    }
                ]
            }
        }
    },
    methods: {
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let opt = this.registerValidateForm;
                    api.userRegister(opt).then(({
                        data
                    }) => {
                        if (data.success) {
                            this.$message({
                                type: 'success',
                                message: ``
                            })
                            //  Register  Login 
                            this.$router.go(0)
                            this.$router.push('/login')
                        } else {
                            this.$message({
                                type: 'info',
                                message: ''
                            })
                        }
                    }).catch((err) => {
                        console.log(err);
                    })
                } else {
                    console.log('Error Submit!!');
                    return false;
                }
            });
        }
    }
}
</script>

<style lang="css">
</style>



